// # Cards store

// All changes to array saves to localStorage

// To change value of array element use this construction:
// $cards[i].content = value

// To get i of element by card id use getIndex store method

// ### Card object structure
// - id (readable number)
// - type (readable string)
// - changeTime (writable number)
// - _state (private writable string)
// - _tags (private writable array with strings)
// - _content (private writable string)

// ### Getters and setters
// - state
//   - getter returns `_state`
//   - setter changes `changeTime` to `Date.now()` and `_state` to `value`
// - tags
//   - getter returns `_tags`
//   - setter changes `changeTime` to `Date.now()` and `_tags` to `value`
// - content
//   - getter returns `_content`
//   - setter changes `changeTime` to `Date.now()` and `_content` to `value`

// ### toJSON
// returns id, type, changeTime, state, tage, content of element

// ### Store methods
// - create(type)
//     adds empty card to array
// - delete(id)
//     deletes element from array by card id
// - getIndex(id)
//     returns i of element by card id
// - clean
//     deletes all array elements

'use strict'

import { writable, get } from 'svelte/store'

class Card {
  constructor(id, type, modifyDate, state, tags, content) {
    this.id = Number(id)
    this.type = String(type)
    this.modifyDate = Number(modifyDate)
    this._state = String(state)
    this._tags = tags
    this._content = String(content)

    Object.defineProperties(this, {
      id: {
        writable: false,
      },
      type: {
        writable: false,
      },
    })
  }

  toJSON() {
    return {
      id: this.id,
      type: this.type,
      modifyDate: this.modifyDate,
      state: this.state,
      tags: this.tags,
      content: this.content,
    }
  }

  modified() {
    this.modifyDate = Date.now()
  }

  get state() {
    return this._state
  }
  set state(value) {
    this._state = value
    this.modified()
  }

  get tags() {
    return this._tags
  }
  set tags(value) {
    this._tags = value
    this.modified()
  }

  get content() {
    return this._content
  }
  set content(value) {
    this._content = value
    this.modified()
  }
}

// If localStorage cards item isn`t exist
if (!localStorage.getItem('cards')) {
  if (!PRODUCTION) console.log('Cards item created!')

  localStorage.setItem('cards', '[]')
}

let parsedCards = JSON.parse(localStorage.getItem('cards')).map(
  (card) =>
    new Card(
      card.id,
      card.type,
      card.modifyDate,
      card.state,
      card.tags,
      card.content
    )
)

function createCardsStore() {
  const { subscribe, set, update } = writable(parsedCards)

  // Saving to localStorage
  subscribe((cards) => {
    localStorage.setItem('cards', JSON.stringify(cards))
    if (!PRODUCTION) console.log('Saved in localStorage')
  })

  return {
    subscribe,
    update,
    set,

    create: (type = 'md') =>
      update((cards) => {
        return [
          new Card(Date.now(), type, Date.now(), 'active', [], ''),
          ...cards,
        ]
      }),

    delete: (id) =>
      update((cards) => {
        return cards.filter((card) => card.id !== Number(id))
      }),

    getIndex: (id) => {
      return get(cards).indexOf(get(cards).find((c) => c.id === id))
    },

    clean: () => set([]),
  }
}

export const cards = createCardsStore()
